import React,{ Component } from 'react';
import {
    BookMainDetailWrapper
}from '../../style.js'
class BookMainDetail extends Component{
    render(){
        const {book}=this.props;
        return(
            <React.Fragment>
                <BookMainDetailWrapper>
                    <div className="goodsMain">
                                    
                        <span className="goodname">:&nbsp;${book.name }</span>
                        <span className="mainTil">:</span>
                        <span className="first">32</span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span>:&gt;&gt;&gt;</span>
                
                    </div>
                </BookMainDetailWrapper>
            </React.Fragment>
        )
    }
}
export default BookMainDetail;