import React, { PureComponent } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { LoginWrapper, LoginBox,Button } from './style';
import { actionCreators } from '../store';
import NavWrapper from '../Header/Nav';
class Login extends PureComponent {
	constructor(props){
		super(props);
		this.state={
			account:"",
			password:""
		}
	}
	handleChangeAccount(e){
		let value=e.target.value;
        value=value.replace(/\D/g,'')
		if(value.length>=12){
			value=value.substring(0,11);
		}
        this.setState({
			account:value
		})

	}
	handleChangePwd(e){
		this.setState({
			password:e.target.value
		})
	}
	checkInput(){
		const {account,password}=this.state;
		if(!account||!password){
			alert("");
		}else if(account.length<11){
			alert("11");
		}
		else if(password.length>12){
			alert("12,,");
		}
		else if(password.match(/^(w|d|_)/g)){
			alert(",,");
		}else {
			this.props.login(account,password);
		}

	}
	render() {
		const {loginState,loginOut,userInfo, isManage}=this.props;
		if (!loginState) {
			return (
				<React.Fragment>
					 <NavWrapper loginState={loginState} loginOut={loginOut} userInfo={userInfo} isManage={isManage}/>
				<LoginWrapper>
					<LoginBox>
						<input type="text" placeholder='' name="account" onChange={this.handleChangeAccount.bind(this)} value={this.state.account}/>
						<input placeholder='' name='password' type="password" onChange={this.handleChangePwd.bind(this)} value={this.state.password}/>
						<i ref={(icon) => {this.showIcon = icon}} className="iconfont show">&#xe675;</i>
						<Button onClick={this.checkInput.bind(this)}></Button>
					</LoginBox>
				</LoginWrapper>
				</React.Fragment>
			)
		}else {
			return <Redirect to='/'/>
		}
	}
}

const mapState = (state) => ({
	loginState: state.getIn(['common', 'login']),
    userInfo:state.getIn(["common","userInfo"]),
    isManage:state.getIn(["common","isManage"])
})

const mapDispatch = (dispatch) => ({
	login(accountElem, passwordElem){
		dispatch(actionCreators.login(accountElem, passwordElem))
	},
    loginOut(){
        dispatch(actionCreators.logout());
    }
    
})


export default connect(mapState, mapDispatch)(Login);